import React,{Component} from 'react'
import Header from './header'
export default class Footer extends Component{
    render()
    {
        return(
            <div>
                <Header/>
                
                <hr className='hori'/>
               
                <div  className='detailinfo'>
                <center>
                <h3>&copy; This website  is developed by Sagar S Bhat  </h3>
                </center>
              
                <hr className='hori'/>
                <div className='container'>
                <h4>This is a website that has been developed by using React and back end api are developed uisng Node.js and Mongodb
                    the deployment is handeled by Heruku and the database is handeled in Mongo Atlas.
                     This is a basic food delevery app that just takes user input and displays it the order form.
                    <br/>
                    <hr/>
                
                     <strong className='strong'>Home Page: </strong>
                     In Home Page location has to be selected first and then based upon the location there is a choice to search restaurant based on restaurantlist and by mealtype. 
                     <br/>
                    <hr/>
                     <strong className='strong'>RestaurantList page: </strong>
                     By selecting meals you get  are directed to this page where the restaurants that provides specific meals are listed and there is a filter and sort present in this page where filters can be made according to the cuisine(North,South etc...) and cost filters are also present. 
                     Here there is also a provision for sorting restaurants based on cost from high to low and vice versa
                     <br/>
                    <hr/>
                     <strong className='strong'>Restaurant Details Page: </strong>
                       This page displays basic information on restaurants and from here orders can be placed
                       This page can be obtained through both Home page and RestaurantList Page.
                       Through Home page by selecting location and meals.
                       in RestaurantList page by selecting on the restaurant 
                       <br/>
                    <hr/>
                         
                     <strong className='strong'>Order Page: </strong>
                     This page is directed by selecting place order in Details Page .Here have to fill basic information and 
                     submit after submitting the displays will be shown.
                     <br/>
                     <hr/>
                     <strong className='strong'> Note : This is a demo project on Restaurant Order and when order is placed it just displays it and does not place the order. </strong>
                     <br/>
                     <hr/>
                </h4>
                </div>
                </div>
            </div>
        )
    }
}