import React,{Component} from 'react'
import  axios from 'axios'

const url='http://localhost:8900/orders'
const rurl='http://localhost:8900/restaurants'

export default class Form extends Component{
      
    constructor(props)
    {
        super(props)

        this.state={
            _id:Math.floor(Math.random()*10000),
            name:'',
            phone: '',
            email: '',
            address:'',
            person: '',
            restid: '',
        }
    }


    renderrest=(data)=>
    {
        if(data)
        {
            return data.map((i)=>
            {
                return i.name
            })
        }

    }
  
    btnback=()=>
    {
        this.props.history.push(`/rest/${this.props.match.params.id}`)
    }


  btnsubmit=()=>
  {
      var data={
        "_id": this.state._id,
        "name": this.state.name,
        "phone":  this.state.phone,
        "email":  this.state.email,
        "address": this.state.address,
        "person":  this.state.person,
        "restid":  this.renderrest(this.state.restid)
      }

      fetch(url,{method:'POST',
    headers:
{
    'Accept':'application/json',
    'Content-Type':'application/json'
},
body:JSON.stringify(data)})
.then(this.props.history.push('/orders'))
  }



  

  rendername=(event)=>
  {
      this.setState({name:event.target.value})
  }



   renderphone=(event)=>
   {
      this.setState({phone:event.target.value})
   }
   
   renderemail=(event)=>
   {
      this.setState({email:event.target.value})
   }
   
   renderaddress=(event)=>
   {
      this.setState({address:event.target.value})
   }
   
   renderperson=(event)=>
   {
      this.setState({person:event.target.value})
   }




    render()
    {
     
        return(

            <React.Fragment>
                <div className='card container'>
                    <div className='card-header bg-primary'>
                        <h4>Place Order</h4>
                    </div>
                    <div className='card-body'>
                    <div className='form-groups'>

                    <label for='urs'><h5><strong>Order Id</strong></h5></label>
                    <input className='form-control' type='text' readonly value={this.state._id}></input> <br/>

                    <label for='urs'><h5><strong>Rest Name</strong></h5></label>
                    <input className='form-control' type='text' readonly  value={this.renderrest(this.state.restid)}></input> <br/>

                    <label for='urs'><h5><strong>Name</strong></h5></label>
                    <input className='form-control' type='text'  value={this.state.name} onChange={this.rendername}></input> <br/>

                    <label for='urs'><h5><strong>Contact No</strong></h5></label>
                    <input className='form-control' type='text' value={this.state.phone}  onChange={this.renderphone}></input> <br/>

                    <label for='urs'><h5><strong>Email</strong></h5></label>
                    <input className='form-control' type='text' value={this.state.email}  onChange={this.renderemail}></input> <br/>

                    <label for='urs'><h5><strong>Address</strong></h5></label>
                    <input className='form-control' type='text' value={this.state.address}  onChange={this.renderaddress}></input> <br/>

                    <label for='urs'><h5><strong>No of Persons</strong></h5></label>
                    <select value={this.state.person} className='form-control'  onChange={this.renderperson}>
                        <option>1</option>
                        <option>2</option>
                        <option>3</option>
                        <option>4</option>
                        <option>5</option>
                    </select>
                </div>
                    </div>
                    <div className='card-footer'>
                        <button className='btn btn-lg btn-danger' onClick={this.btnback}>Back</button>
                        &nbsp;&nbsp;&nbsp;
                        <button className='btn btn-lg btn-success' onClick={this.btnsubmit}>Proceed</button>
                    </div>
                </div>
                
            </React.Fragment>
        )
    }

    componentDidMount()
    {
      
        axios.get(`${rurl}/${this.props.match.params.id}`)
        .then((response)=>
        {
          this.setState({restid:response.data})
         
        })
    }
}
