import React,{Component} from 'react'
import {Link} from 'react-router-dom'
import axios from 'axios'

export default class Restdetail extends Component{
  
   
   constructor(props)
   {
       super(props)

       this.state={
           activePage:15
       }
   }







    renderrest=(data)=>
    {
      if(data)
      {
          return data.map((i)=>
          {
              return(
                  <React.Fragment>
                   
                      <div className='listmain'  style={{marginTop:'20px'}}>
                     
                          <div className='card-body' >
                              <Link to={`/rest/${i._id}`}>
                              <div className='row'>
                              <div className='col-2 list'>
                              <img src={i.thumb} className='img2'></img>
                              </div>
                              
                              <div className='col-10  card-title list2'>
                                  {i.name}
                              </div>
                            
                              </div>
                              </Link>
                              <div className='card-footer'>
                                  <div className='row'>
                                      <div className='col-4'>
                                      <div className='list3'>
                                      Cuisine:<br/>
                                      {i.Cuisine.map((i)=>
                                      {
                                          return (
                                              <div className='list4'>
                                                  {i.name}<br/>
                                                  </div>
                                          )
                                      })}
                                  </div>
                                      </div>
                                    
                                      <div className='col-5'>
                                      <div className='list3'>
                                      Cost<br/>
                                     <div className='list'>Average cost of 2 is {i.cost}</div>
                                  </div>

                                  </div>
                                  
                              </div>
                          </div>
                      </div>
                      </div>
                  </React.Fragment>
              )
          })
      }
     
      
    }



  









    render()
    {
        return(
            <React.Fragment>
                                                                                                                                                                    
            <div className='container-fluid'>
                {this.renderrest(this.props.restdata)}
            </div>
            </React.Fragment>
        )
    }
}