import React, { Component } from "react";
import Header from "./header";

export default class Orderdisplay extends Component {
  constructor(props) {
    super(props);
  }

  renderrest = (data) => {
    if (data) {
      return data.map((i) => {
        return (
          <tr>
            <td>{i._id}</td>
            <td>{i.name}</td>
            <td>{i.phone}</td>
            <td>{i.email}</td>
            <td>{i.person}</td>
            <td>{i.restid}</td>
            <td>{i.address}</td>
          </tr>
        );
      });
    }
  };

  homebtn = () => {};

  render() {
    return (
      <React.Fragment>
        <Header />
        <div className="card container">
          <div className="card-header bg-primary">
            <h1>Order Placed Successfully</h1>
          </div>
          <div className="card-body">
            <div className="container">
              <table className="table table-bordered table-responsive">
                <thead>
                  <tr>
                    <th>Order Id</th>
                    <th>Name</th>
                    <th>Contact</th>
                    <th>Email</th>
                    <th>Persons</th>
                    <th>Restaurant</th>
                    <th>Address</th>
                  </tr>
                </thead>
                <tbody>{this.renderrest(this.props.data)}</tbody>
              </table>
            </div>
          </div>
          <div className="card-footer bg-light">
            <center>
              <h1 className="text-success">Thanks For Using SFOODS</h1>
            </center>
          </div>
        </div>
      </React.Fragment>
    );
  }
}
