import React,{Component} from 'react'
import Header from  './header'
import Search from './Search'
import Quick from  './quicksearch'
import axios from 'axios'


const url='http://localhost:8900/cities'

const murl='http://localhost:8900/meals'

export default class Body extends Component{
    constructor()
    {
        super()

        this.state={

            city:'',
            meal:''

        }
    }


   resthandle=(sorteddata)=>
   {
       this.props.history.push(`/rest/${sorteddata}`)
   }




    render()
    {
        return(
            <div>
                <Header/>
                <Search restdata={(data)=>{this.resthandle(data)}}            citydata={this.state.city}/>
                <Quick    mealId={this.state.meal}/>
            </div>
        )
    }
    
    componentDidMount()
    {
        fetch(url,{method:'Get'})
        .then((res)=>res.json())
        .then((data)=>
        {
            this.setState({city:data})
        })



        axios.get(murl).then((response)=>
        {
            this.setState({meal:response.data})
        })
    }
    
}