import React, { Component } from "react";
import { Link } from "react-router-dom";
import axios from "axios";

const url = "http://localhost:8900/cuisine";

const rurl = "http://localhost:8900/restaurantlist";

export default class CuisineFilter extends Component {
  constructor(props) {
    super(props);

    this.state = {
      meals: "",
      
    };
  }

  rendercuisine = (event) => {
  
    this.props.restdata(event.target.value)
   

  };




  renderrest = (data) => {
    if (data) {
      return data.map((i) => {
        return (
          <React.Fragment>
            <label className="label">
              <input
                type="radio"
                name="cuisine"
                value={i.cuisine}
                onChange={this.rendercuisine}
              ></input>{" "}
              &nbsp;  {i.name}
            </label>
            <br />
          </React.Fragment>
        );
      });
    }
  };

  render() {
    return (
      <div>
       
       
       <span className='title1'> Cuisine</span>
        <br />
        <hr/>
      

       {this.renderrest(this.state.meals)}
      </div>
    );
  }

  componentDidMount() {
    axios.get(`${url}`).then((response) => {
      this.setState({ meals: response.data });
    });
  }
}
