import React, { Component } from "react";
import { Link } from "react-router-dom";
import axios from "axios";
import Header from './header';
import CuisineFilter from "./cuisinefilter";
import Restdetail from "./restdetail";
import SortFilter from "./sortfilter";
import CostFilter from "./costfilter";

const url = "http://localhost:8900/restaurantlist";
const murl = "http://localhost:8900/meals";
const lurl = "http://localhost:8900/city";

export default class Rest extends Component {
  constructor(props) {
    super(props);

    this.state = {
      cityId: parseInt(localStorage.getItem("cityId"))
        ? parseInt(localStorage.getItem("cityId"))
        : 1,

      rest: "",
      loc: "",
      m: "",
      cuisine: "",
      sort: "",
      lcost: "",
      hcost: "",
    };
  }

 



 
    restcuisinefilter = (sorteddata) => {

      var cuisine=sorteddata;
      this.setState({cuisine:cuisine})
   

     
    }

    restasort = (sorteddata) => {
      
      var sort=sorteddata;
      this.setState({sort:sort})

    

    };

    restacost = (sorteddata) => {
      
      var cost=sorteddata.split(',');
      var lcosts=cost[0]
      var hcosts=cost[1]
      this.setState({lcost:lcosts})

      this.setState({hcost:hcosts})

      


      
    };





 



   


  renderloc = (data) => {
    if (data) {
      return data[0].city_name;
    }
  };

  renderm = (data) => {
    if (data) {
      return data.map((i) => {
        return i.name;
      });
    }
  };

  render() {

  
  
    

    return (
      <div>
        <Header />
        <div className="offset-xl-1">
          <div
            className="display-4"
            style={{ color: "darkslateblue", fontWeight: "400" }}
          >
            {this.renderloc(this.state.loc)}
            's {this.renderm(this.state.m)} places
          </div>
          <div className="container-fluid"  onChange={this.renderApi}>
            <div className="row">
              <div className="col-md-2 hidden-sm">
                <div className="filter d-none d-lg-block">
                  <div className="cuisine">
                    <div className="title2">Filters</div>
                    <hr />
                    <CuisineFilter
                      restdata={(data) => {
                        this.restcuisinefilter(data);
                      }}
                      mealId={this.props.match.params.id}
                      cityIdno={this.state.cityId}
                    />
                    <hr />
                  </div>
                  <div className="cuisine">
                    <CostFilter
                      restcost={(data) => {
                        this.restacost(data);
                      }}
                      mealId={this.props.match.params.id}
                      cityIdno={this.state.cityId}
                    />
                    <hr />
                  </div>
                  <div className="cuisine">
                    <div className="sort">
                      <SortFilter
                        restsort={(data) => {
                          this.restasort(data);
                        }}
                        mealId={this.props.match.params.id}
                        cityIdno={this.state.cityId}
                      />
                    </div>
                  </div>
                </div>
              </div>
              <div className="col-md-10">
                <div className="restaurant">
                  <Restdetail restdata={this.state.rest} />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
  componentDidMount() {
    localStorage.setItem("mealdata", this.props.match.params.id);

    console.log(`${lurl}/${this.state.cityId}`);
    axios
      .get(`${url}/${this.state.cityId}/${this.props.match.params.id}`)
      .then((response) => {
        this.setState({ rest: response.data });
      });

    axios.get(`${lurl}/${this.state.cityId}`).then((response) => {
      this.setState({ loc: response.data });
    });

    axios.get(`${murl}/${this.props.match.params.id}`).then((response) => {
      this.setState({ m: response.data });
    });
  }

  componentDidUpdate()
  {
   
    var mainurl;
      if(this.state.cuisine!='' && this.state.lcost!='' &&  this.state.hcost!='' &&  this.state.sort!='')
      {
         mainurl=`${url}/${this.state.cityId}/${this.props.match.params.id}?cuisine=${this.state.cuisine}&lcost=${this.state.lcost}
         &hcost=${this.state.hcost}&sort=${this.state.sort}`
      }

      else if(this.state.cuisine!='' && this.state.lcost!='' &&  this.state.hcost!='' &&  this.state.sort=='')
      {
        mainurl=`${url}/${this.state.cityId}/${this.props.match.params.id}?cuisine=${this.state.cuisine}&lcost=${this.state.lcost}
        &hcost=${this.state.hcost}`

      }

      else if(this.state.cuisine!='' && this.state.lcost=='' &&  this.state.hcost=='' &&  this.state.sort!='')
      {
          mainurl=`${url}/${this.state.cityId}/${this.props.match.params.id}?cuisine=${this.state.cuisine}&sort=${this.state.sort}`
      }
      
      else if(this.state.cuisine=='' && this.state.lcost!='' &&  this.state.hcost!='' &&  this.state.sort!='')
      {
        mainurl=`${url}/${this.state.cityId}/${this.props.match.params.id}?lcost=${this.state.lcost}
        &hcost=${this.state.hcost}&sort=${this.state.sort}`
      }
      else if(this.state.cuisine=='' && this.state.lcost!='' &&  this.state.hcost!='' &&  this.state.sort=='')
      {
        mainurl= `${url}/${this.state.cityId}/${this.props.match.params.id}?lcost=${this.state.lcost}
        &hcost=${this.state.hcost}`
      }

      else if(this.state.cuisine!='' && this.state.lcost=='' &&  this.state.hcost=='' &&  this.state.sort=='')
      {
        mainurl=`${url}/${this.state.cityId}/${this.props.match.params.id}?cuisine=${this.state.cuisine}`
      }

      else if(this.state.cuisine=='' && this.state.lcost=='' &&  this.state.hcost=='' &&  this.state.sort!='')
      {
        mainurl=`${url}/${this.state.cityId}/${this.props.match.params.id}?sort=${this.state.sort}`
      }

      else{
        mainurl=`${url}/${this.state.cityId}/${this.props.match.params.id}`
      }











    axios.get(mainurl)
    .then((response)=>
    {
      this.setState({rest:response.data})
    })

    console.log(mainurl)
  }



}
