import React, { Component } from "react";
import axios from "axios";
import { Link } from "react-router-dom";
import Header from "./header";
import { Tab, Tabs, TabList, TabPanel } from "react-tabs";
import "react-tabs/style/react-tabs.css";
import {Carousel} from 'react-responsive-carousel'
import 'react-responsive-carousel/lib/styles/carousel.min.css'

const url = "http://localhost:8900/restaurants";

export default class Detail extends Component {
  constructor(props) {
    super(props);

    this.state = {
      rest: "",
    };
  }

 btnsubmit=()=>
 {
   this.props.history.push(`/orders/${this.props.match.params.id}`)
 }

 btncancel=()=>
 {

   this.props.history.push(`/restaurant/${localStorage.getItem('mealdata')}`)
 }

 






  renderrest = (data) => {
    if (data) {
      return data.map((i) => {
        return (
          <React.Fragment>
            <div className="container">
              <img src={i.thumb} className='img1'></img>
              <div className="title">{i.name}</div>
              <div className='tabs'>
                <Tabs>
                  <TabList>
                    <Tab>
                      <span className='overview'>Overview</span>
                    </Tab>
                    <Tab>
                      <span className='overview'>Contact</span>
                    </Tab>
                  </TabList>
                  <TabPanel>
                    <div className="aboutinfo">About This Restaurant</div>
                    <div className="info2">
                      Cuisine
                      <br />
                      <div className="info3">
                        {i.Cuisine.map((i) => {
                          return (
                            <div>
                              {i.name}
                              <br />
                            </div>
                          );
                        })}
                      </div>
                      <div className="info2">
                        Cost
                        <br />
                        <div className="info3">Average Cost is {i.cost}</div>
                      </div>
                    </div>
                  </TabPanel>
                  <TabPanel>
                      <div className='aboutinfo'>Phone Number</div>
                      <div className='info2' style={{color:'red'}}>
                          {i.contact_number}
                      </div>
                      <div className='info2'>
                          {i.name}
                      </div>
                      <div className='info2'>Address<br/>
                      <div className='info3'>{i.address}</div>
                      </div>

                  </TabPanel>
                </Tabs>
              </div>
              <div>
                
                <button className='btn btn-lg btn-danger' on onClick={this.btncancel}>Back</button>
                &nbsp;&nbsp;
                <button className='btn btn-lg btn-success' onClick={this.btnsubmit}>Proceed</button>
              </div>
            </div>
          </React.Fragment>
        );
      });
    }
  };

  render() {
    return (
      <div>
        <Header />
        <div className="container" style={{height:'1350px'}}>{this.renderrest(this.state.rest)}</div>
      </div>
    );
  }
  componentDidMount() {
    axios(`${url}/${this.props.match.params.id}`).then((response) => {
      this.setState({ rest: response.data });
    });
  }
}
