import React, { Component } from "react";
import { Link } from "react-router-dom";
import axios from "axios";

const url = "http://localhost:8900/cities";
const resturl = "http://localhost:8900/rest";

export default class Search extends Component {
  constructor() {
    super();

    this.state = {
      city: "",
      loc: "",
      rest: "",
    };
  }

  rendercity = (event) => {
    this.setState({ loc: event.target.value });
    var cityid = parseInt(event.target.value);
    localStorage.setItem("cityId", event.target.value);
    axios.get(`${resturl}/${cityid}`).then((response) => {
      this.setState({ rest: response.data });
    });
  };

  renderrest = (data) => {
    if (data) {
      return data.map((i) => {
        return (
          <option value={i._id}>
            {i.name} | {i.locality}
          </option>
        );
      });
    }
  };

   rendername=(event)=>
   {
     this.props.restdata(event.target.value)

   }








  renderloc = (data) => {
    if (data) {
      return data.map((i) => {
        return <option value={i.city}>{i.name}</option>;
      });
    }
  };

  render() {
    return (
      <React.Fragment>
        <div className="img">
          <div className="container">
              <div className='logo1'>
            <div className="logo">s!</div>
            </div>
          <div className="txt">Find best restaurants cafes and bars</div>

          <div className="select">
            <span className="select1">
              <select onChange={this.rendercity}>
                {this.renderloc(this.props.citydata)}
              </select>
            </span>

            <span className="select1">
            
    <select id="data" onChange={this.rendername}  style={{height:'45px',width:'400px'}}>{this.renderrest(this.state.rest)}  </select>
            </span>
          </div>
        </div>
        </div>
      </React.Fragment>
    );
  }
}
