import React,{Component} from 'react'
import {Link} from 'react-router-dom'

export default class Header extends Component{
    render()
    {
        return(
            <div>
                <nav className='navbar navbar-expand-lg  bg-danger navbar-dark'>
                    <div className='container-fluid'>
                        <div className='navbar-header'>
                            <button type='button' className='navbar-toggler' data-toggle='collapse' data-target='#mynavbar'>
                                <i className='navbar-toggler-icon'></i>
                            </button>
                            <Link className='navbar-brand'>SFOODS</Link>
                            </div>
                            <div className='collapse navbar-collapse' id='mynavbar'>
                    <ul className='navbar-nav'>
                        <li className='nav-item active'><Link to='/' className='nav-link'>Home</Link></li>
                        <li className='nav-item active'><Link to='/details'   className='nav-link'>Details</Link></li>
                    </ul>
                    </div>
                        </div>

                   
                </nav>
            </div>
        )
    }
}