import React, { Component } from "react";
import { Link } from "react-router-dom";
import axios from "axios";

const rurl = "http://localhost:8900/restaurantlist";
export default class SortFilter extends Component {
  constructor(props) {
    super(props);

    
  }

  rendersort = (event) => {
   this.props.restsort(event.target.value)

  
  };

  render() {
     
    return (
      <React.Fragment>
         <span className='title1'>Sort</span>
        <hr />
        <label className="label">
          <input
            type="radio"
            name="sort"
            value="-1"
            onChange={this.rendersort}
          />
           &nbsp;Price High to Low
        </label>
        <br />
        <label className="label">
          <input
            type="radio"
            name="sort"
            value="1"
            onChange={this.rendersort}
          />
           &nbsp; Price Low to High
        </label>
        <br />
      </React.Fragment>
    );
  }
}
