import React,{Component} from 'react'
import {BrowserRouter,Route} from 'react-router-dom'
import Body from './Components/body'
import Footer from './Components/footer'
import Rest from './Components/restlist'
import Detail from './Components/details'
import Form from './Components/forms'
import Order from './Components/orderview'
import './Components/CSS/rest.css'


export default class Routing extends Component{
    render()
    {
        return(
            <BrowserRouter>

            <div>
                <Route exact path='/' component={Body}></Route>
                <Route exact path='/details' component={Footer}></Route>
                <Route exact path='/restaurant/:id' component={Rest}></Route>
               <Route exact path='/rest/:id'  component={Detail}></Route>
              <Route exact path='/orders/:id' component={Form}></Route>
              <Route exact path='/orders' component={Order}></Route>
            </div>
            </BrowserRouter>
        )
    }
}
