import React, { Component } from "react";
import { Link } from "react-router-dom";
import axios from "axios";

const rurl = "http://localhost:8900/restaurantlist";

export default class CostFilter extends Component {
  constructor(props) {
    super(props);

  }

  rendercost = (event) => {
   this.props.restcost(event.target.value)

  
  };

  render() {
    return (
      <div onChange={this.rendercost}>
       <span className='title1'> Cost</span>
       <hr/>
      
        <label className="label">
          <input type="radio" value="0,500" name="cost" />
          &nbsp; 0-500
        </label>
        <br />
        <label className="label">
          <input type="radio" value="500,1000" name="cost" />
          &nbsp; 500-1000
        </label>
        <br />
        <label className="label">
          <input type="radio" value="1000,1500" name="cost" />
          &nbsp; 1000-1500
        </label>
        <br />
      </div>
    );
  }
}
